znie zaplecze logistyczne – zajmujemy się zleceniami od początku do końca. Sami generujemy pomiary w domu Klienta, sami projektujemy, produkujemy i przewozimy, wychodząc z założenia, że żadna pośrednicząca firma transportowa nie odniosłaby się do naszych produktów z dostatecznym zaangażowaniem. Dzięki takiemu podejściu, mimo rozwoju firmy, nie zapomnieliśmy o celu, jaki przyświecał nam od początku, i jakim kierujemy się do dziś. Bezpośredni, osobisty wręcz, kontakt z Klientem pozwala nam kształtować relacje oparte na wzajemnym szacunku i zaufaniu. ")]),_c('p',{staticClass:"about-text"},[_vm._v(" W chwili obecnej zajmujemy się produkcją oraz sprzedażą wysokiej klasy mebli pokojowych i kuchennych, typowych, jak również na wymiar. W naszej kolekcji znajdą coś dla siebie nie tylko amatorzy klasyki, ale także ci, którzy najlepiej czują się wśród mebli nowoczesnych, o progresywnym szlifie. Dużą popularnością cieszy się prezentowana przez nas gama szaf przesuwnych. Meble, produkowane przez Meble Gniatkowski, wykonywane są w oparciu o nowoczesne projekty, opracowane przez doświadczonych i cenionych designerów. Jako producent dbamy, aby nasze meble wyróżniały się wysoką jakością wykonania. Jakością, którą sygnuję nazwiskiem. Serdecznie zapraszam do zapoznania się z naszą ofertą ")])])}]

